import React, { Component, useState, useEffect } from 'react';

import './Game.css';


import Blob from './Blob';

import bg from '../../assets/gamebg.svg';
import grass from '../../assets/grass.svg';
import grassDivider from '../../assets/grassDivider.svg';

import btnbg from '../../assets/btnbg.svg';

import iblob from '../../assets/blob.svg';
import iblobIll from '../../assets/blobIll.svg';

import restart from '../../assets/restart.svg';
import move from '../../assets/move.svg';
import hammer from '../../assets/hammer.svg';

const Game = () => {

    
    const [gen, setGen] = useState(2);
    const [blobsLen, setBlobsLen] = useState(2);

    const [blobs, setBlobs] = useState([new Blob(1, 0, false, null, [null, null], 0), new Blob(1, 1, false, null, [null, null], 1)]);

    useEffect(() => {
        const interval = setInterval(() => {
            blobs.forEach(blob => {

                if (!blob.dead) {
                    setTimeout(() => {
                        if (!blob.moving) {
                            blob.move();
                            blob.moving = true;

                            document.getElementById(blob.id).classList.add('moving');

                            setTimeout(() => {
                                if (!blob.dead) {
                                    blob.moving = false;
                                    document.getElementById(blob.id).classList.remove('moving');
                                }
                            }, blob.speed * 1000);
                        }
                    }, blob.timeout * 1000);
                }
            });
        }, 500);
        return () => clearInterval(interval);
    }, [blobs]);


    const create = () => {
        
    }

    const handleGenClick = () => {

        console.log(blobsLen)

        let temp = [];
        let ind = 0

        if (gen >= 2) {
            
            let currLen = blobsLen;
            blobs.forEach(blob => {

                if (blob.gen === gen - 2) {
                    blob.die();
                    // setBlobsLen(blobsLen => blobsLen -= 1);

                    blob.dead = true;
                    setBlobsLen(blobsLen => blobsLen -= 1);
                    
                }

            });
            
        }

        let coupleInd = blobs.length - blobsLen;
        
        
        for (let i = 0; i < Math.floor(blobsLen / 2); i++) {
            let kids = Math.floor(Math.random() * (3 - 1) + 1);
            console.log('--> ' + kids)
            console.log('--> ' + blobs[coupleInd].id + ' ' + blobs[coupleInd + 1].id)
            setBlobsLen(blobsLen => blobsLen + kids);

            for (let j = 0; j < kids; j++) {

                let ill;
                if (blobs[coupleInd].ill && blobs[coupleInd + 1].ill) {
                    ill = (Math.random() * 100) <= 90;
                    console.log('90%');
                }
                else if ((blobs[coupleInd].ill && !blobs[coupleInd + 1].ill) || (!blobs[coupleInd].ill && blobs[coupleInd + 1].ill)) {
                    ill = (Math.random() * 100) <= 50;
                    console.log('50%');
                }
                else {
                    ill = (Math.random() * 100) <= 30;
                    console.log('30%');
                }

                let illness;
                if (ill) {
                    if (blobs[coupleInd].ill && blobs[coupleInd + 1].ill) {
                        if (blobs[coupleInd].illness === blobs[coupleInd + 1]) {   
                            illness = blobs[coupleInd].illness;
                        }
                        else if (blobs[coupleInd].illness !== blobs[coupleInd + 1]) {
                            if (Math.floor(Math.random() * (100 - 1) + 1) < 50) {
                                illness = blobs[coupleInd].illness;
                            }
                            else {
                                illness = blobs[coupleInd + 1].illness;
                            }
                        }
                    }
                    else if (blobs[coupleInd].ill && !blobs[coupleInd + 1].ill) {
                        illness = blobs[coupleInd].illness;
                    }
                    else if (!blobs[coupleInd].ill && blobs[coupleInd + 1].ill) {
                        illness = blobs[coupleInd + 1].illness;
                    }
                    else {
                        let chance = Math.floor(Math.random() * (100 - 1) + 1);
                        if (chance < 20) {
                            illness = 'down';
                        }
                        else if (chance > 20 && chance < 40) {
                            illness = 'klainfeltur';
                        }
                        else if (chance > 40 && chance < 60) {
                            illness = 'ultraman';
                        }
                        else if (chance > 60 && chance < 80) {
                            illness = 'cat';
                        }
                        else if (chance > 80) {
                            illness = 'albinism';
                        }
                    }
                }

                temp.push(new Blob(gen, ind, ill, illness, [blobs[coupleInd], blobs[coupleInd + 1]], Math.floor(Math.random() * (2 - 1) + 1)));
                ind++;
            }

            coupleInd += 2;
        }

        setBlobs(blobs => blobs.concat(temp));
        setGen(gen => gen + 1);
        
    }


    const handleRestartClick = () => {
        
    }


    const handleGenClickTest = () => {

        for (let i = blobs.length - blobsLen; i < (blobs.length - (blobsLen % 2)) / 2; i += 2) {

            let midTop = (blobs[i].top + blobs[i + 1].top) / 2;
            let midLeft = (blobs[i].left + blobs[i + 1].left) / 2;

            blobs[i].moving = true;
            blobs[i + 1].moving = true;

            document.getElementById(blobs[i].id).style.top = midTop - 20 + 'px';
            document.getElementById(blobs[i].id).style.left = midLeft - 20 + 'px';

            document.getElementById(blobs[i + 1].id).style.top = midTop + 20 + 'px';
            document.getElementById(blobs[i + 1].id).style.left = midLeft + 20 + 'px';

            if (i === (blobs.length - (blobsLen % 2)) / 2 - 1) {
                setTimeout(() => {
                    create();
                }, 1000);
            }
        }

    }


    return (
        <div id="game">

            <img id="grass" src={ grass } alt="" />

            <button id="genbtn" onClick={ handleGenClick }>НОВА ГЕНЕРАЦИЯ</button>

            <div id="toolbar" style={{display: 'none'}}>
                <div className="tboption" id="killbtn">
                    <img src={ hammer } alt="" />
                </div>
                <div className="tboption" id="movebtn">
                    <img src={ move } alt="" />
                </div>
                <div className="tboption" id="restartbtn" onClick={ handleRestartClick }>
                    <img src={ restart } alt="" />
                </div>

            </div>

            {blobs.map((blob) => {
                
                return (
                    <div key={ blob.id }>
                        <img onLoad={ blob.show } src={ blob.img } onClick={ blob.move } id={ blob.id } className={"blob " + (blob.dead ? 'dead' : '')}
                        style={{top: blob.top + 'px', left: blob.left + 'px', transition: 'top ' + blob.speed + 's, left ' + blob.speed + 's, transform .5s'}} />
                    </div>
                    // <div onClick={ blob.move } id={ blob.id } className={"blob " + (blob.ill ? 'ill' : '')} 
                    // style={{top: blob.top + 'px', left: blob.left + 'px', transition: 'top ' + blob.speed + 's, left ' + blob.speed + 's, transform .5s'}}>
                    //     <label>{blob.id}</label><br />
                    //     {blob.parents[0] && 
                    //         <label style={{color: 'black'}}>{blob.parents[0].id} - {blob.parents[1].id}</label>
                    //     }
                    // </div>
                )
                
            })}

        </div>
    );
}
 
export default Game;


