import React, { Component } from 'react';

import './InfoSlide.css';

import iblob from '../../assets/blob.svg';


const InfoSlide = (slide) => {

    const props = slide.props;

    return (
        <div key={props.id} className="infoslide">
            <div className={"iscont " + props.side}>

                <div className="isheader">
                    <label>{ props.title }</label>
                </div>
                <div className="istop">
                    <div className="istleft">
                        <label className="isdata">{ props.mainData }</label>
                        <label className="issubheader">Признаци</label>
                        <label className="issubdata">{ props.signs }</label>
                    </div>
                    <div className="istright">
                        <img src={ props.img } alt="" />
                    </div>
                </div>
                <div className="isbottom">
                    <div className="isbleft">
                        <label className="issubheader">Причини</label>
                        <label className="issubdata">{ props.reasons }</label>
                    </div>
                    <div className="isbright">
                        <label className="issubheader">Лечение</label>
                        <label className="issubdata">{ props.treatment }</label>
                    </div>
                </div>
                {props.facts && (
                    <div className="isfact">
                        <label className="issubheader">Интересни факти</label>
                        <label className="issubdata">{ props.facts }</label>
                    </div>
                )}

            </div>
        </div>
    );
}
 
export default InfoSlide;
