import React, { Component } from 'react';

import iblob from '../../assets/blob.svg';
import blobAlb from '../../assets/blobAlb.svg';
import blobDown from '../../assets/blobDown.svg';
import blobKlain from '../../assets/blobKlain.svg';
import blobUltra from '../../assets/blobUltra.svg';
import blobCat from '../../assets/blobCat.svg';


class Blob {

    constructor(gen, ind, ill, illness, parents, timeout) {

        this.gen = gen;
        this.ind = ind;

        this.id = gen + '.' + ind;

        this.ill = ill;
        this.illness = illness;
        this.illnessNum = (illness === 'albinism' ? 1: illness === 'down' ? 2 : null);

        this.parents = parents;
        
        this.img = (illness === 'albinism' ? blobAlb : illness === 'down' ? blobDown : illness === 'klainfeltur' ? blobKlain : illness === 'ultraman' ? blobUltra : illness === 'cat' ? blobCat : iblob);

        this.top = Math.floor(Math.random() * (window.innerHeight - 150));
        this.left = Math.floor(Math.random() * (window.innerWidth - 90));

        this.speed = 2;
        this.timeout = timeout;

        this.moving = false;

        this.dead = false;
    }

    move = () => {

        if (!this.dead) {
            let moveTop = Math.floor(Math.random() * (window.innerHeight - 150));
            let moveLeft = Math.floor(Math.random() * (window.innerWidth - 90));

            let distance = Math.hypot(moveLeft - this.left, moveTop - this.top);
            
            this.speed = Math.ceil(distance / 300) * 2;
        
            this.top = moveTop;
            this.left = moveLeft;
        
            document.getElementById(this.id).style.transition = 'top ' + this.speed + 's linear, left ' + this.speed + 's linear, transform .5s linear';

            document.getElementById(this.id).style.top = this.top + 'px';
            document.getElementById(this.id).style.left = this.left + 'px';
        }
        
    }

    show = () => {
        document.getElementById(this.id).style.transform = 'scale(1)';
    }

    die = () => {
        document.getElementById(this.id).style.transform = 'scale(0)';
    }


    test = () => {
        document.getElementById(this.id).style.transform = 'scale(.1)';
    }

    // render() {
        
    //     // let top = Math.floor(Math.random() * (window.innerHeight - 55));
    //     // let left = Math.floor(Math.random() * (window.innerWidth - 55));

    //     // console.log(top, left)

    //     return (
    //         <div onClick={this.move} className="blob" style={{top: this.state.top + 'px', left: this.state.left + 'px', transition: 'all ' + this.state.speed + 's'}}>
            
    //         </div>
    //     )
    // }

}


export default Blob;

