import React, { Component } from 'react';

import './LandingPage.css';

const LandingPage = () => {
    return (
        <div id="landingpage">
            <div id="lpcont">
                <h1>Наследствени болести<br />при човека</h1>
                <p>Наследствените болести при човека са болестите, чието протичане се променя незначително дори при силно влияние на факторите на средата и са генетично детермирани. Те се разделят на две основни групи – хромозомни и молекулни болести.</p>
            </div>
        </div>
    );
}
 
export default LandingPage;

